/* eslint-disable new-cap */

export const inspectString = (value, { preferSingleQuote }) => {
  return escapeString(value, { preferSingleQuote })
}

// https://github.com/nodejs/node/blob/31d1d0c4c19fea5007eb8c55a4cb1178f295c8ca/lib/internal/util/inspect.js#L463
const escapeString = (str, { preferSingleQuote }) => {
  const escapeSingleQuote = preferSingleQuote

  let result = ""
  let last = 0
  const lastIndex = str.length
  for (let i = 0; i < lastIndex; i++) {
    const point = str.charCodeAt(i)
    if (
      (escapeSingleQuote && point === 39) ||
      point === 92 ||
      point < 32 ||
      (point > 126 && point < 160) ||
      // line separators
      point === 8232 ||
      point === 8233
    ) {
      // eslint-disable-next-line no-nested-ternary
      const replacement = point === 8232 ? "\\u2028" : point === 8233 ? "\\u2029" : meta[point]

      if (last === i) {
        result += replacement
      } else {
        result += `${str.slice(last, i)}${replacement}`
      }
      last = i + 1
    }
  }

  if (last !== lastIndex) {
    result += str.slice(last)
  }
  return quote(result, { preferSingleQuote })
}

const quote = (string, { preferSingleQuote }) => {
  if (preferSingleQuote) {
    return `'${string}'`
  }
  return `"${string}"`
}

// prettier-ignore
const meta = [
  '\\x00', '\\x01', '\\x02', '\\x03', '\\x04', '\\x05', '\\x06', '\\x07', // x07
  '\\b', '\\t', '\\n', '\\x0B', '\\f', '\\r', '\\x0E', '\\x0F',           // x0F
  '\\x10', '\\x11', '\\x12', '\\x13', '\\x14', '\\x15', '\\x16', '\\x17', // x17
  '\\x18', '\\x19', '\\x1A', '\\x1B', '\\x1C', '\\x1D', '\\x1E', '\\x1F', // x1F
  '', '', '', '', '', '', '', "\\'", '', '', '', '', '', '', '', '',      // x2F
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',         // x3F
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',         // x4F
  '', '', '', '', '', '', '', '', '', '', '', '', '\\\\', '', '', '',     // x5F
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',         // x6F
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '\\x7F',    // x7F
  '\\x80', '\\x81', '\\x82', '\\x83', '\\x84', '\\x85', '\\x86', '\\x87', // x87
  '\\x88', '\\x89', '\\x8A', '\\x8B', '\\x8C', '\\x8D', '\\x8E', '\\x8F', // x8F
  '\\x90', '\\x91', '\\x92', '\\x93', '\\x94', '\\x95', '\\x96', '\\x97', // x97
  '\\x98', '\\x99', '\\x9A', '\\x9B', '\\x9C', '\\x9D', '\\x9E', '\\x9F', // x9F
];
